<template>
	<div class="personal-content-body">
		<div class="table_content">
			<div class="table_content_title flex_center">
				<div v-for="item in topNav" class="top_nav cursor">
					<router-link :to="{name: item.component}">
						<span>{{ item.text }}</span>
					</router-link>
				</div>
			</div>
			<div class="table_content_body">
				<router-view></router-view>
			</div>
		</div>

	</div>
</template>

<script>
	import { dialogConfirm, dialogConfirmHasbtn } from '../../config/mUtils'
	import { getRouterListApi } from '../../config/api'
	
	export default {
		name: 'fundManageHome',
		data(){
			return {
				rootid: '',
				topNav: [
//					{
//						component: "projectManageHome",
//						text: '项目管理'
//					},
//					{
//						component: "creditQueryHome",
//						text: '征信查询'
//					},
//					{
//						component: "tuneUpAssistant",
//						text: '尽调助手'
//					},
				]
			}
		},
		created(){
			this.getRouterList()
		},
		methods: {
			// 获取导航路由数据
			getRouterList(){
				getRouterListApi({
					specMenuId: 15
				}).then(res => {
					if(res.data.msgNo == 1) {
						this.topNav = res.data.payload;
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
		}
	}
</script>

<style scoped="scoped">
	
</style>


